<template>
  <div class="my-pages">
    <the-navbar />
    <main class="home main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <h1 class="h3 text-center mb-5">Mis Páginas</h1>
          </div>
          <div
            class="col-12 col-sm-4 col-lg-3"
            v-for="(item, index) in myPages"
            :key="index"
          >
            <Site :image="item.image" :name="item.name" :slug="item.slug" />
          </div>
        </div>
      </div>
    </main>
    <div class="container">
      <div class="row">
        <div class="col-12">
          <the-footer />
        </div>
      </div>
    </div>
    <pre class="container">{{ $data }}</pre>
  </div>
</template>

<script>
import { fb } from "@/firebase";
import { db } from "@/main";
import axios from "axios";

import Site from "@/components/Site";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "MyPages",
  components: {
    Site,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      myPages: []
    };
  },
  created() {
    this.getMyPages();
  },
  methods: {
    async getMyPages() {
      const user = await fb.auth().currentUser;

      await axios(`${db}/users/${user.uid}/pages`)
        .then(result => {
          this.myPages = result.data;
        })
        .catch(error => {
          var errorCode = error.code;
          var errorMessage = error.message;
          if (errorCode == "auth/weak-password") {
            alert("The password is too weak");
          } else {
            alert(errorMessage);
          }
          console.log(error);
        });
    }
  }
};
</script>

<style scoped></style>
